/* global chrome, $, tilt_direction, sync2, authController */

import React, { Component } from 'react';
import BoardBlock from './BoardBlock';
import CreateBoard from './CreateBoard';

class FolderBlock extends Component {
    constructor(props) {
        super(props);
        // this.addFolder = this.addFolder.bind(this);
        this.deleteFolder = this.deleteFolder.bind(this);
        this.changeHandler = this.changeHandler.bind(this);
        this.update = this.update.bind(this);
        this.refreshBoard = this.refreshBoard.bind(this);

        this.folder = chrome.extension.getBackgroundPage().getFolder();  
        this.board = chrome.extension.getBackgroundPage().getBoard();
        this.service = chrome.extension.getBackgroundPage().getService();
        
        this.state = this.props.item;
        this.state.removed = false;
        this.state.boards = this.board.getByFolder(this.props.item.id);
    }

    deleteFolder(id) {

        window.confirmBox.callback = () => {
            this.folder.delete(id);
            this.setState({  removed: true });  
            
            this.board.getByFolder(id).forEach(item => {
                this.board.delete(item.id);
            });

            window.chrome.extension.getBackgroundPage().createContextMenu();

            var data = new FormData();

            data.append('ids', [id]);
    		data.append('via', 'extension');
    	 
            this.service.post(this.service.version + "delete-folder", data)
                .then(function(response) { }); 
            $('#confirm_box').modal('hide');
        }
        window.confirmBox.init('Are you sure, you want to remove this folder?');

    //    if(window.confirm('Are you sure, you want to remove this folder?')) {
    //         this.folder.delete(id);
    //         this.setState({  removed: true });  
            
    //         this.board.getByFolder(id).forEach(item => {
    //             this.board.delete(item.id);
    //         });

    //         window.chrome.extension.getBackgroundPage().createContextMenu();

    //         var data = new FormData();

    //         data.append('ids', [id]);
    // 		data.append('via', 'extension');
    	 
    //         this.service.post(this.service.version + "delete-folder", data)
    //             .then(function(response) { });           
    //     }
        
    }

    changeHandler(event) {
        const name = event.target.name;
        
        this.setState({
            [name]: event.target.value
        });
    }

    update(id) {
       this.folder.update(id, {
           title: this.state.title.trim(),
           sync: "true"
       });
       this.setState({
            title: this.state.title.trim()
       });
       this.service.createOrUpdateFolder(id, function(response){ });
        
    }

    refreshBoard() {
        this.setState({
            boards: this.board.getByFolder(this.props.item.id),
        }); 
    }
    
    componentDidMount() {
        var that = this;
        this.$node = $(this.refs.sortable);
        this.$node.sortable({
            handle: ".dragable-element",
            connectWith: '.boards-container',
            placeholder: "ui-state-highlight col-sm-4 white_box",
            start: (event, ui) => {
                ui.item.addClass('tilt');
                tilt_direction(ui.item);
            },            
            stop: function (e, ui) {
                ui.item.removeClass("tilt");

                var collection = [];

                $.map($(this).find('.q-board-block'), function (el) {
                    
                    collection.push($(el).attr('id'));
                    that.board.update($(el).attr('id'), {
                        sortable: $(el).index()
                    });
                });
                var formData = new FormData();
                formData.append('boards', collection);
                that.service.post(that.service.version + 'board/change-order', formData)
                .then(function(response) {
                    console.log("response");
                    console.log(response);

                })
                .catch(function(response){ });
            },
            receive: function (event, ui) {
                
                that.board.update(ui.item.attr('id'), {
                    folder_id: that.state.id,
                    sortable: ui.item.index()
                });

                that.service.createOrUpdateBoard(ui.item.attr('id'), function(response){
                    console.log(response);
                });

            }
        });
    }

    render() {
        return (
            (this.state.removed === false)? (
                <div class="row board-row " >
                    <div class="col-sm-12 media align-items-center my-3">
                        <div class="media-left pr-2">
                            <img src="asset/image/folder.png" height="15" />
                        </div>
                        <div class="media-body">
                            <h4 class="media-body m-0"> 
                                <input type="text" 
                                    name="title"
                                    value={this.state.title || ''}
                                    onBlur={() => this.update(this.state.id) }
                                    onChange={this.changeHandler}
                                    placeholder="Unnamed Folder"
                                    className="folder-name-editor" />
                            </h4>
                        </div>
                        <div onClick={() => this.deleteFolder(this.state.id)} class="media-right link-hover" >
                            <img src="asset/image/delete.png"/>
                        </div>
                    </div>
                    <div class="col-md-12">
                        <div ref="sortable" class="row boards-container">
                            { this.state.boards.map((board) => ( <BoardBlock board={board} refreshBoard={this.refreshBoard}  /> )) }                    
                            <CreateBoard folder_id={this.state.id} refreshBoard={this.refreshBoard}  />
                        </div>
                    </div>
                </div>
            ) : null
        );
    }
      
}

export default FolderBlock;
