/* global chrome, $, tilt_direction, sync2, authController */
import React, { Component } from 'react';

class CreateBoard extends Component {

    constructor(props) {
        super(props);
        this.board = chrome.extension.getBackgroundPage().getBoard();
        this.create = this.create.bind(this);
        this.openForm = this.openForm.bind(this);
        this.changeHandler = this.changeHandler.bind(this);
        this.state = {
            form: false,
            boardname: ''
        };
    }

    changeHandler(event) {
        const name = event.target.name;

        this.setState({
            [name]: event.target.value
        });
    }

    create(e) {

        e.preventDefault();
        
        if(!authController.canCreateMoreBoard()) {
            alert('Please upgrade your plan to create more board.');
        } else {

            this.board.create({
                folder_id: this.props.folder_id,
                name: this.state.boardname
            });

            this.setState({
                form: false,
                boardname: ''
            });

            sync2(function(){}, true);
            
            this.props.refreshBoard();
        }
    }

    openForm() {
        this.setState({
            form: true
        });
    }

    render() {
        return (
            <div class="col-md-4 col-sm-6 px-2 d-flex justify-content-strech" draggable="false">
                { (this.state.form == true)? (
                    <div class="white_box w-100">
                        <div class="media">
                            <form className="w-100" onSubmit={this.create} >
                                <div class="media-body">
                                    <div class="form-group">
                                        <input type="text"  autofocus="true" class="form-control" placeholder="Board Name" value={this.state.boardname} name="boardname" onChange={this.changeHandler} />
                                    </div>
                                    <button type="submit" class="btn btn-default btn-block">Create</button>                            
                                </div>
                            </form>
                        </div>
                    </div>
                ) : (
                    <div class="white_box dotted_box w-100" onClick={this.openForm} >
                        <a class="link-hover">
                        <div class="media p-3 d-flex align-content-center">
                            <div class="media-left align-self-center d-none">
                                <img src="asset/image/plus_icon.png" width="20" />
                            </div>
                            <div class="media-body text-center align-self-center p-3">
                                <img src="asset/image/plus_icon.png" width="20" /> Create Board
                            </div>
                        </div>
                        </a>
                    </div>
                ) }
          </div>
        );
    }
}


export default CreateBoard;
