/* global StripeCheckout, chrome, $, service, Confetti */
import React, { Component } from 'react';

class CurrentPlan extends Component {

  render() {
    return (
      <button type="button" class="button button--accent-outline" >
        Current Plan
      </button>
    );
  }
}


class Billing extends Component {

    constructor(props) {
        super(props); 
        this.tax = 0;
        this.state = {            
            handler: null,
            currentPlan: null,
            discountAmount: 0,
            validatingPromode: false,
            promocode_err: '',
            promocode_applied_msg: '',
            plan: 'bignner',
            duration: 'lifetime',
            bignner: {
              monthly: 0,
              yearly: 0,
              lifetime: 0,
              discount: 0,
            },

            premium: {
              monthly: 5,
              yearly: 36,
              lifetime: 49,
              discount: 0
            },

            business: {
              monthly: 20,
              yearly: 180,
              lifetime: 199,
              discount: 0
            },

            enterprise: {
              monthly: 60,
              yearly: 540,
              lifetime: 186,
              discount: 0
            }
        }
        this.service = chrome.extension.getBackgroundPage().getService();
        this.auth = chrome.extension.getBackgroundPage().getAuth();
        this.upgrade = this.upgrade.bind(this);
        this.cancelSubscription = this.cancelSubscription.bind(this);
        this.duration = this.duration.bind(this);
        this.applyPromocode = this.applyPromocode.bind(this);
        this.upgradePlan = this.upgradePlan.bind(this);
        this.getPlan = this.getPlan.bind(this);
    }

     componentDidMount () {
        var that = this;

        var subscription = this.auth.subscription();
        if(subscription) {          
          this.setState( { 
            plan : (subscription.plan || '').toLowerCase(),
            currentPlan : subscription
          });
        }

        this.service.get(this.service.version + 'current-subscription')
          .then( function( respponse ) {            
            that.setState({ currentPlan: respponse.data });
            that.auth.setSubscription(respponse.data);
          });

        if(!document.getElementById('stripe_script')) {
            var s = document.createElement('script');
            s.src = chrome.extension.getURL('asset/js/conffeti.min.js');
          
            const script = document.createElement("script");

            script.src = "https://checkout.stripe.com/checkout.js";
            script.async = true;
            script.id = "stripe_script";
              //key: 'pk_test_MsS8Ufzmm2KpTn4x5Du1oQab',
            script.onload = function() {
              // key: 'pk_live_dEhrYLnZEnUfeJj0kiRoFmoc',
              
              that.handler = StripeCheckout.configure({              
                  key: 'pk_live_dEhrYLnZEnUfeJj0kiRoFmoc',
                  image: 'https://qlearly.com/beta/extension_web/img/Qlearlylogo.png',
                  locale: 'auto',
                  token: function(token) {
                    
                    if(!token.id) {
                      alert('Oops! Something went wrong');
                      return;
                    }                
                    that.upgradePlan(token);
                  }
              }); 
            }

          document.body.appendChild(script);
          document.body.appendChild(s);
        }
    }

    upgradePlan(token) {

      $('.big-centered-loader').show();

      var that = this;
      var data = new FormData();

      data.append('token', token.id);
      data.append('plan', that.state.plan );
      data.append('duration', that.state.duration );
      data.append('promocode', that.refs.promocode.value );
      data.append('discount', that.state.discountAmount );

      var data = {}
      
      data.token = token.id;
      data.plan =that.state.plan;
      data.duration = that.state.duration;
      data.promocode = that.refs.promocode.value;
      data.discount = that.state.discountAmount;

      

      service.post(that.service.version + "pay", data)
        .then( function( response ) {
          $('.big-centered-loader').hide();
          that.auth.setSubscription(response.data);
          that.setState({ currentPlan: response.data });
          that.resetPromocode.call(that);

          new Confetti({
            width    : window.innerWidth,
            height   : window.innerHeight,
            length   : 220,
            duration : 8000
          });

          setTimeout(() => {
              $('canvas').remove();
          }, 8000);  

        })
        .catch(function(response){
          that.resetPromocode.call(that);
          $('.big-centered-loader').hide();
        });

    }

    addTax(amount) {
      return parseFloat(parseFloat(amount + ( ( amount * this.tax ) / 100)).toFixed(2));
    }

    selectPlan(plan) {
        this.setState({
            plan: plan
        });
    }

    selectDuration(duration) {
        this.setState({
            duration: duration
        });

        this.resetPromocode.call(this);
    }

    upgrade(plan) {
        var that = this;
        this.setState({
            plan: plan,
            discountAmount: this.state[plan].discount
        }, function() {
          if(that.state[plan][that.state.duration] >= 0) {
            let amtAfterDiscount = that.getPayAmount(plan);
            if(!that.handler) {
              alert('Oops! Please wait library is loading')
              return;
            }
            if(amtAfterDiscount <= 0 && that.state.duration === 'lifetime') {
              that.upgradePlan({}); 
            } else {
              that.handler.open({
                name: plan.ucfirst(),
                description: that.state.duration.ucfirst() + ' Plan',
                amount: that.addTax( amtAfterDiscount * 100 ),
              });
            }
          }

        });


    }

    cancelSubscription() {
      this.setState({
          currentPlan: null
      });
     
      var data = new FormData();

      this.service.post(this.service.version + "cancel-subscription", data)
        .then( function( respponse ) {
          $('.loading').hide();
        })
        .catch(function(respponse){ });
    }


  planContent() {
    if (!this.state.currentPlan) {
      return 'Your current plan is the Beginner plan';
    }

    var months = ['January', 'Feburary', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
    var expireDate = new Date();
    if(this.state.currentPlan.expire_at) {
      expireDate = new Date(this.state.currentPlan.expire_at);
    }

    return  (<span>Your current plan is the <b>{this.state.currentPlan.plan} </b> plan. It will renew on {months[expireDate.getMonth()]}, {expireDate.getDate()} { expireDate.getFullYear()}</span>);
  }

  duration() {
    if(this.state.duration == 'monthly') {
      return 'month';
    } else if(this.state.duration == 'yearly') {
      return 'year';
    } else {
      return 'lifetime';
    }
  }

  applyPromocode() {

    var that = this;

    if(!this.refs.promocode.value) {
      that.setState({ promocode_err: 'Please enter the promocode', promocode_applied_msg: '' });
      return;
    }
    
    this.setState({ validatingPromode: true, promocode_err: '', promocode_applied_msg: '' });
    var fd = new FormData();
    fd.append('promocode', this.refs.promocode.value);
    fd.append('duration', this.state.duration);

    this.service.post(this.service.version + 'validate-promocode', fd)
    .then( function( response ) {

        that.setState({ validatingPromode: false });
        if(response.success === false) {          
          that.setState({ promocode_err: response.msg });
        } else {

          that.state.bignner.discount = response.data.bignner_amount;
          that.state.business.discount = response.data.business_amount;
          that.state.premium.discount = response.data.premium_amount;
 
          that.setState({ 
            promocode_applied_msg: response.msg,             
            premium : that.state.premium,
            business : that.state.business,
            bignner : that.state.bignner,
          });

        }

    });

  }

  resetPromocode() {
    this.state.bignner.discount = 0;
    this.state.business.discount = 0;
    this.state.premium.discount = 0;

    this.refs.promocode.value = '';
    this.setState({ 
      validatingPromode: false,
      promocode_err: '',
      promocode_applied_msg: '',
      discountAmount: 0,
      premium : this.state.premium,
      business : this.state.business,
      bignner : this.state.bignner
    });
  }

  getPayAmount(plan) {
    let amount = this.state[plan][this.state.duration] - this.state[plan].discount;
    if(this.state.duration === 'lifetime') {
      if(amount > 0 && amount < .50) {
        amount = 0.5;
      }
    }
    return parseFloat(parseFloat(amount).toFixed(2));
  }

  getPlan() {
    
    if(this.state.currentPlan) {
      console.log(this.state.currentPlan.plan);
      
      return (this.state.currentPlan.plan || '').toLowerCase();
    }

    return 'bignner';
  }

  render() {
    return (
        <div class="white_box row mb-5 animated fadeInUp">
           <div class="border col-sm-5 p-3 text-muted">
            { this.planContent() }
           </div>
           <div class="col-sm-7 text-muted border-right border-top border-bottom">
              <div class="row">
                 <div class="col-12 p-3 border-bottom">
                    <label class="d-flex align-items-center link-hover" onClick={() => this.selectDuration('monthly')}>
                        <span class="radioinput">
                            <input type="radio" name="subscription" defaultChecked={this.state.duration == 'monthly'} />
                             <span></span>
                        </span> &nbsp;&nbsp; Monthly
                    </label>
                 </div>
                 <div class="col-12 p-3 d-flex border-bottom">
                    <label class="d-flex align-items-center link-hover" onClick={() => this.selectDuration('yearly')}>
                        <span class="radioinput">
                            <input type="radio" name="subscription"  defaultChecked={this.state.duration == 'yearly'}  />
                             <span></span>
                        </span> &nbsp;&nbsp; Yearly
                    </label>                    
                 </div>
                 <div class="col-12 p-3 d-flex">
                    <label class="d-flex align-items-center link-hover" onClick={() => this.selectDuration('lifetime')}>
                        <span class="radioinput">
                            <input type="radio" name="subscription"  defaultChecked={this.state.duration == 'lifetime'}  />
                             <span></span>
                        </span> &nbsp;&nbsp; Limited Lifetime Plan 
                    </label>                    
                 </div>
              </div>
           </div>

          <div className={this.state.duration == 'lifetime'? 'col-sm-12 px-0 lifetime' : 'col-sm-12 px-0' }>
            <div class="pricing-02__cards_container">
                  <ul class="pricing-02__cards_list d-flex">                    
                    <li className={this.state.plan == 'bignner'? 'pricing-02__card_box pricing-02__card_box--accent flex-grow-1 mt-4 mx-2' : 'pricing-02__card_box flex-grow-1 mt-4 mx-2'}  onClick={() => this.selectPlan('bignner')}>
                      <div class="pricing-02__card"><img class="pricing-02__card_img" src="/asset/image/chiken.png"/>
                        <h3 class="pricing-02__card_title">Beginner</h3>
                        <div class="pricing-02__price_box">
                          <div class="pricing-02__price_item js-tab-content state-active-tab" data-index="yearly-price-1" data-group="my-pricing-group-2">
                            <div class="pricing-02__price_symbol">$</div>
                            <div class="pricing-02__price">{ this.getPayAmount('bignner') }</div>
                            <div class="pricing-02__period width-50">/{this.duration()}</div>
                          </div>
                        </div>
                        <ul class="pricing-02__info">
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">∞ Bookmarks</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">Save an unlimited amount of tabs when browsing around the web!</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">2 Teammates</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">Working on a project with a team? You can have 2 teammates join your boards.</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">15 Boards</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">Organize your bookmarks further by using different boards. You can create up to 15 boards for free.</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">Device Syncing</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">Sync your bookmarks across multiple devices. Mobile browsers included!</div>
                            </div>
                          </li>
                        </ul>
                        <div class="pricing-02__card_button">
                          { (!this.state.currentPlan)? ( <CurrentPlan /> ) : (
                              (this.getPlan() === 'bignner') ? ( <CurrentPlan />  ) : (
                              <button type="button" class="button button--accent-bg"
                                onClick={() => this.upgrade('bignner')} >
                                Upgrade
                              </button>
                            ) 
                          )
                          }
                          
                        </div>
                      </div>
                    </li>
                    <li className={this.state.plan == 'premium'? 'pricing-02__card_box pricing-02__card_box--accent flex-grow-1 mt-4 mx-2' : 'pricing-02__card_box flex-grow-1 mt-4 mx-2'}  onClick={() => this.selectPlan('premium')}>
                      <div class="pricing-02__card"><img class="pricing-02__card_img" src="/asset/image/imac.png"/>
                        <h3 class="pricing-02__card_title">Premium</h3>
                        <div class="pricing-02__price_box">
                          <div class="pricing-02__price_item js-tab-content state-active-tab" data-index="yearly-price-1" data-group="my-pricing-group-2">
                            <div class="pricing-02__price_symbol">$</div>
                            <div class="pricing-02__price">{ this.getPayAmount('premium') }</div>
                            <div class="pricing-02__period">/{this.duration()}</div>
                          </div>
                        </div>
                        <ul class="pricing-02__info">
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">No Ads</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">Free accounts may have sublte Ads. Get rid of them by upgrading!</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">5 Teammates</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">You can have up to 5 teammates join your boards. They won't need to upgrade, just have them register and invite them!</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">&infin; Boards</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">Organize your bookmarks further by using different boards. You can create up to unlimited boards with this plan.</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">Priority Support</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">We are always here to help! You can expect to hear back from us within 24 hours.</div>
                            </div>
                          </li>
                        </ul>
                        <div class="pricing-02__card_button">
                          { ( this.getPlan() === 'premium') ? ( <CurrentPlan />  ) : (
                            <button type="button" class="button button--accent-bg"
                              onClick={() => this.upgrade('premium')} >
                              Upgrade
                            </button>
                          ) }
                          
                        </div>
                      </div>
                    </li>
                    <li className={this.state.plan == 'business'? 'pricing-02__card_box pricing-02__card_box--accent flex-grow-1 mt-4 mx-2' : 'pricing-02__card_box flex-grow-1 mt-4 mx-2'}  onClick={() => this.selectPlan('business')}>
                      <div class="pricing-02__card"><img class="pricing-02__card_img" src="/asset/image/case.png"/>
                        <h3 class="pricing-02__card_title">Business</h3>
                        <div class="pricing-02__price_box">
                          <div class="pricing-02__price_item js-tab-content state-active-tab" data-index="yearly-price-1" data-group="my-pricing-group-2">
                            <div class="pricing-02__price_symbol">$</div>
                            <div class="pricing-02__price">{this.getPayAmount('business') }</div>
                            <div class="pricing-02__period">/{this.duration()}</div>                            
                          </div>
                        </div>
                        <ul class="pricing-02__info">
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">All features</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">Access all the features available on Qlearly. Including Device Syncing and No Ads, ever!</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">15 Teammates</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">You can have up to 15 teammates join your boards. They won't need to upgrade, just have them register and invite them!</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">&infin; Boards</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">Organize your bookmarks further by using different boards. Create Unlimited boards with our business plan.</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">Priority Support</div>
                                <img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">We are always here to help! You can expect to hear back from us within 24 hours.</div>
                            </div>
                          </li>
                        </ul>
                        <div class="pricing-02__card_button">
                        { (this.getPlan() === 'business') ? ( <CurrentPlan /> ) : (
                            <button type="button" 
                              class="button button--accent-bg"
                              onClick={() => this.upgrade('business')} >
                                Upgrade
                            </button>
                          ) }
                        </div>
                      </div>
                    </li>


                    <li className={this.state.plan == 'enterprise'? 'pricing-02__card_box pricing-02__card_box--accent flex-grow-1 mt-4 mx-2 d-none' : 'pricing-02__card_box flex-grow-1 mt-4 mx-2  d-none'} onClick={() => this.selectPlan('enterprise')} >
                      <div class="pricing-02__card"><img class="pricing-02__card_img" src="/asset/image/building.png"/>
                        <h3 class="pricing-02__card_title">Enterprise</h3>
                        <div class="pricing-02__price_box">
                          <div class="pricing-02__price_item js-tab-content state-active-tab">
                            <div class="pricing-02__price_symbol">$</div>
                            <div class="pricing-02__price">{(this.state.duration =='monthly')? ( this.state.enterprise.monthly ) : ( this.state.enterprise.yearly ) }</div>
                            <div class="pricing-02__period">/{ (this.state.duration =='monthly')? ( 'month' ) : ( 'year' ) }</div>
                          </div>
                        </div>
                        <ul class="pricing-02__info">
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">All features</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">Access all the features available on Qlearly. Including Device Syncing and No Ads, ever!</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">&infin; Teammates</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">Working with a big team? Get access to unlimited teammates with our enterprise plan. You will only be charged for one user!</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">&infin; Boards</div><img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">Organize your bookmarks further by using different boards. With our Enterprise plan, you can invite an unlimited amount of users to your boards.</div>
                            </div>
                          </li>
                          <li class="pricing-02__info_item">
                            <div class="pricing-02__info_item_box">
                              <div class="pricing-02__info_item_text">Priority Support</div>
                                <img class="pricing-02__info_item_icon js-hover-to-show-sibling" src="/asset/image/help.svg"/>
                              <div class="pricing-02__hover js-content-to-show">We are always here to help! You can expect to hear back from us within 24 hours.</div>
                            </div>
                          </li>
                        </ul>
                        <div class="pricing-02__card_button">
                         { (this.getPlan() === 'enterprise') ? ( <CurrentPlan /> ) : (
                            <button type="button" class="button button--accent-bg"
                              onClick={() => this.upgrade('enterprise')} >
                                Upgrade
                            </button>
                          ) }

                        </div>
                      </div>
                    </li>
                </ul>
            </div>
          </div>
          <div class="border col-sm-12 mt-4 p-3 text-muted ">
            <div className="row align-items-start">            

              <div class="text-center col-lg-4 py-3">
                  <div class="d-flex align-items-center justify-content-center">
                      <input type="text" name="promocode" ref="promocode" id="promocode" class="form-control mr-2 form-control-lg" placeholder="Promocode" />
                      { (this.state.promocode_applied_msg)? (
                          <button class="btn btn-primary" onClick={this.resetPromocode.bind(this)}>X</button>
                        ) : (
                          (this.state.validatingPromode)? (  
                            <button class="btn btn-primary btn-lg">Validating...</button>
                          ) : (
                            <button class="btn btn-primary btn-lg" onClick={() => this.applyPromocode()}>Apply</button>
                          )
                        )
                      }                  
                  </div>
                  
                  { (this.state.promocode_applied_msg)? (
                    <label className="text-success">{ this.state.promocode_applied_msg }</label>
                  ) : null }
                  { (this.state.promocode_err)? (
                    <label className="text-danger">{ this.state.promocode_err }</label>
                  ) : null }
                  
                </div>
                <div className="text-center col-lg-4 py-3">
                    <a href="https://qlearly.com/paypal" target="_blank" class="btn btn-warning btn-lg btn-block d-flex justify-content-center">
                     Check out with &nbsp;<img src="/asset/image/paypal.png" height="19"/> 
                    </a>
                </div>
            
                <div className="text-center col-lg-4 py-3">
                  
                      { (!this.state.currentPlan)? (
                          <button class="btn btn-primary btn-lg btn-block" onClick={() => this.upgrade(this.state.plan)}>Upgrade</button>
                        ): ( 
                          <button class="btn btn-primary btn-lg btn-block" onClick={this.cancelSubscription}>Cancel Subscription</button>
                      )}
                  
                </div>  
              </div>          
          </div>
        </div>
    );
  }
}

export default Billing;
