/*global chrome, auth, signup */
import React, { Component } from 'react';

class AccountSetting extends Component {

	constructor(props) {
	    super(props);
	    this.toggerTheme = this.toggerTheme.bind(this);
	    this.saveSettings = this.saveSettings.bind(this);
	    this.toggerTabSetting = this.toggerTabSetting.bind(this);
	    this.toggerOpenTabsSetting = this.toggerOpenTabsSetting.bind(this);
	    this.service  = chrome.extension.getBackgroundPage().getService();
	    this.board  = chrome.extension.getBackgroundPage().getBoard();

	    this.state = {
	    	sync_board: (localStorage.getItem("sync_board") == "true"),
	    	changeTheme: (localStorage.getItem("changeThemeColor") == "true"),
	    	save_pinned_tabs: (localStorage.getItem("save_pinned_tabs") == "true"),
	    	tab_status_on_save: (localStorage.getItem("tab_status_on_save") == "true"),
	    	save_duplicate_url: (localStorage.getItem("save_duplicate_url") == "true"),
	    	open_links_in_new_tabs: (localStorage.getItem("open_links_in_new_tabs") == "true"),
	    	search_all_or_specific_board: (localStorage.getItem("search_all_or_specific_board") == "true"),
	    	isLoggedIn: auth.isLoggedIn()
	    };
	 }

	toggerTheme (e) {


		if(!this.state.isLoggedIn) {
			if(e.target.checked === true) {
				e.target.checked = false;
			} else {
				e.target.checked = true;
			}
			signup();
			return false;
		}
		this.setState({
			changeTheme : this.refs.changeTheme.checked
		});

    	if(this.refs.changeTheme.checked) {
    		document.body.classList.add('darktheme');
    		localStorage.setItem("changeThemeColor", "true");
    	} else {
    		document.body.classList.remove('darktheme');
    		localStorage.setItem("changeThemeColor", "false");
    	}
  	}

  	toggerTabSetting (e) {

  		if(!this.state.isLoggedIn) {
			if(e.target.checked === true) {
				e.target.checked = false;
			} else {
				e.target.checked = true;
			}
			signup();
			return false;
		}

    	if(this.refs.tab_status_on_save.checked) {
    		localStorage.setItem("tab_status_on_save", "true");
    	} else {
    		localStorage.setItem("tab_status_on_save", "false");
    	}
  	}

  	toggerOpenTabsSetting (e) {
    	
    	if(!this.state.isLoggedIn) {
			if(e.target.checked === true) {
				e.target.checked = false;
			} else {
				e.target.checked = true;
			}
			signup();
			return false;
		}

    	localStorage.setItem("open_links_in_new_tabs", "false");
    	localStorage.setItem("search_all_or_specific_board", "false");    	
    	localStorage.setItem("save_duplicate_url", "false");
    	localStorage.setItem("save_pinned_tabs", "false");

    	if(this.refs.open_links_in_new_tabs.checked) {
    		localStorage.setItem("open_links_in_new_tabs", "true");
    	}

    	if(this.refs.search_all_or_specific_board.checked) {
    		localStorage.setItem("search_all_or_specific_board", "true");
    	}

    	this.saveSettings();
    	if(this.refs.sync_board.checked) {
    		localStorage.setItem("sync_board", "true");
    	} else {
    		localStorage.setItem("sync_board", "false");
    	}

    	
    	if(this.refs.save_duplicate_url.checked) {
    		localStorage.setItem("save_duplicate_url", "true");
    	}

    	if(this.refs.save_pinned_tabs.checked) {
    		localStorage.setItem("save_pinned_tabs", "true");
    	}

  	}

  	saveSettings() {
  		var synboard = String(this.refs.sync_board.checked);
  		if(synboard !== localStorage.getItem("sync_board")) {
    		
    		let para = new FormData();

			var default_column = (this.board.default().id)? this.board.default().id : '';
    		para.append('sync_board', synboard);
            para.append('default_column', default_column);
            
            this.service.post(this.service.version + 'settings', para).then(function(response) { 
            	console.log(response);
            });

    	}

  	}

  render() {
    return (         
        <div className="white_box animated fadeInUp">
  
                <div class="col-sm-12 pb-4">
                	<h4 class="headingSec">Account Setting</h4>
                </div>
            	<div class="col-sm-12 pb-4 white_box border">
		          	<div class="d-flex">
		            	<div class="new_tab">
		              		<input name="ff" ref="changeTheme" type="checkbox" onClick={this.toggerTheme} defaultChecked={this.state.changeTheme} />
		              		<div class="dots"></div>
		            	</div>
		            	<p class="flex-grow-1 line-height20 colorGry mb-0">
		            	 &nbsp;	{ this.state.changeTheme? 'Change to light theme' : 'Change to dark theme' }
		            	</p>
		          	</div>
		        </div>

		        <div class="col-sm-12 pb-4 white_box border">
		          	<div class="d-flex">
		            	<div class="new_tab">
		              		<input name="close_tab" ref="tab_status_on_save" type="checkbox" onClick={this.toggerTabSetting}  defaultChecked={this.state.tab_status_on_save} />
		              		<div class="dots"></div>
		            	</div>
		            	<p class="flex-grow-1 line-height20 colorGry  mb-0"> &nbsp; Close tabs on save</p>
		          	</div>
		        </div>

		        <div class="col-sm-12 pb-4 white_box border">
		          	<div class="d-flex">
		            	<div class="new_tab">
		              		<input name="close_tab" ref="open_links_in_new_tabs" type="checkbox" onClick={this.toggerOpenTabsSetting}  defaultChecked={this.state.open_links_in_new_tabs} />
		              		<div class="dots"></div>
		            	</div>
		            	<p class="flex-grow-1 line-height20 colorGry mb-0"> &nbsp; Open links in a new tab</p>
		          	</div>
		        </div>

		        <div class="col-sm-12 pb-4 white_box border">
		          	<div class="d-flex">
		            	<div class="new_tab">
		              		<input name="close_tab" ref="search_all_or_specific_board" type="checkbox" onClick={this.toggerOpenTabsSetting}  defaultChecked={this.state.search_all_or_specific_board} />
		              		<div class="dots"></div>
		            	</div>
		            	<p class="flex-grow-1 line-height20 colorGry mb-0"> &nbsp; Search: All boards / specific board</p>
		          	</div>
		        </div>

		        <div class="col-sm-12 pb-4 white_box border">
		          	<div class="d-flex">
		            	<div class="new_tab">
		              		<input name="sync_board" ref="sync_board" type="checkbox" onClick={this.toggerOpenTabsSetting}  defaultChecked={this.state.sync_board} />
		              		<div class="dots"></div>
		            	</div>
		            	<p class="flex-grow-1 line-height20 colorGry mb-0"> &nbsp; Sync primary board between devices</p>
		          	</div>
		        </div>

		        <div class="col-sm-12 pb-4 white_box border">
		          	<div class="d-flex">
		            	<div class="new_tab">
		              		<input name="save_duplicate_url" ref="save_duplicate_url" type="checkbox" onClick={this.toggerOpenTabsSetting}  defaultChecked={this.state.save_duplicate_url} />
		              		<div class="dots"></div>
		            	</div>
		            	<p class="flex-grow-1 line-height20 colorGry mb-0"> &nbsp; Save duplicates</p>
		          	</div>
		        </div>

		        <div class="col-sm-12 pb-4 white_box border">
		          	<div class="d-flex">
		            	<div class="new_tab">
		              		<input name="save_pinned_tabs" ref="save_pinned_tabs" type="checkbox" onClick={this.toggerOpenTabsSetting}  defaultChecked={this.state.save_pinned_tabs} />
		              		<div class="dots"></div>
		            	</div>
		            	<p class="flex-grow-1 line-height20 colorGry mb-0"> &nbsp; Save pinned tabs</p>
		          	</div>
		        </div>

            </div>
            
    );
  }
}

export default AccountSetting;
