/* global chrome, $, RSStorage, open_sharable_modal, currentBoard , currentSession, sync2, BoardCtrl*/
import React, { Component } from 'react';

class BoardBlock extends Component {

    constructor(props) {
        super(props);
        this.state = this.props.board;           
        this.edit = this.edit.bind(this);
        this.canShareBoard = (this.props.share === 'false')? false : true;        
        this.share = this.share.bind(this);
        this.delete = this.delete.bind(this);
        this.update = this.update.bind(this);
        this.entry = chrome.extension.getBackgroundPage().getEntry();
        this.board = chrome.extension.getBackgroundPage().getBoard();
        this.state.entry_count = this.entry.getByBoard(this.state.id).length;
        this.state.renderBlock = true;
        this.state.editForm = false;
        this.changeHandler = this.changeHandler.bind(this);
        this.selectBoard = this.selectBoard.bind(this);
        this.makeFavorite = this.makeFavorite.bind(this);    
        this.tempBoard = this.tempBoard.bind(this);
    }

    componentDidMount() {

        var that = this;
        if(localStorage.getItem("sync_board") == "false") {
            if(localStorage.getItem("board_for_new_tab") == this.state.id) {
                $(this.refs.make_favorite).prop('checked', true);
            }
        } else if(this.state.is_favorite == true) {
            $(this.refs.make_favorite).prop('checked', true);
        }

        this.$board = $(this.refs.dropable);
        
        

        this.$board.droppable({
            drop: function (event, ui) {
                
                if(ui.helper.attr('data-tabid')) {
                    let boardDetail = that.board.first(that.state.id);
                    let tab = currentSession[ui.helper.attr('data-tabid')];
                    if(boardDetail.column.length < 1) {
                        boardDetail.column.push(that.board.createNewColumn(that.state.id, {}));
                    }

                    that.entry.create({
                        board_id: that.state.id,
                        column_id: boardDetail.column[0].id,
                        title: tab.title,
                        url: tab.url,
                        favIconUrl: tab.favIconUrl,
                        sortable: that.entry.lastSortable(boardDetail.column[0].id) + 1
                    })

                    that.setState({ entry_count: that.state.entry_count + 1 });
                    ui.helper.remove();
                }
            }
        });
    }

    delete() { 
        window.confirmBox.callback = () => {
            this.board.delete(this.state.id);
            this.setState({ renderBlock: false });
            $('#confirm_box').modal('hide');
        }
        window.confirmBox.init('Are you sure, you want to remove this board?');
    }

    edit() {
        var that = this;
        this.setState({ editForm: true }, function(){
            $(that.refs.name).focus();
        });
    }
    update() {
        this.setState({ editForm: false });
        this.board.update(this.state.id, {
            name: this.state.name
        });
        sync2();
    }

    share() {
        open_sharable_modal(this.state.id);
    }

    changeHandler(event) {
        const name = event.target.name;

        this.setState({
            [name]: event.target.value
        });
    }

    selectBoard() {
        RSStorage.setCurrentBoardId(this.state.id);
    }
    
    makeFavorite() {
        this.board.removeFavorite();

        if(localStorage.getItem("sync_board") == 'true') {
            this.board.updateById(this.state.id, {
                is_favorite: 1
            });
        } else {
            localStorage.setItem("board_for_new_tab", this.state.id);
        }
        this.selectBoard();
    }

    tempBoard() {
        currentBoard.id = this.state.id;
    }

    exportBoard = () => {
        BoardCtrl.exportData(this.state.id);
    }

    createDuplicate = () => {        
        BoardCtrl.copyBoard(this.state.id);
        this.props.refreshBoard();
    }

    render() {
        return (   
            (this.state.renderBlock)? (
                <div className="col-md-4 col-sm-6 px-2 q-board-block dragable-element" id={this.state.id} ref="dropable">
                { (this.state.editForm === false)? (
                    <a href="board-detail.html" className="newfulllink board-select-btn dragable-element" onClick={this.selectBoard}></a>
                ): null }
                <div className="white_box">
                    <div className="media">
                        <div className="media-body mbody100">
                            { (this.state.editForm === false)? (
                            <h4 className="mb-1 text-truncate">
                                <a href="board-detail.html" className="board-select-btn" onClick={this.selectBoard}>
                                { this.state.name.ucfirst() || 'Unnamed board' }
                                </a>
                            </h4>
                            ) : (
                            <div>
                                <form onSubmit={this.update}>
                                    <input type="text" autofocus="true" value={this.state.name} className="board-name-to-edit" onChange={this.changeHandler} name="name" onBlur={this.update} ref="name" />
                                </form>
                            </div>
                            ) }
                            <span className="text-grey mb-3">
                                { this.state.entry_count + ' ' }
                                { (this.state.entry_count > 1)? 'Entries' : 'Entry' }
                            </span>
                            <div className="clearfix"></div>
                            <br />
                            <span className="text-grey">{ (this.state.created_at).toDate() } </span>
                        </div>
                        <div className="media-right">
                            <div className="fancyradio" title="Make it your new tab">
                                <input type="radio" value={this.state.id} name="make_favorite" ref="make_favorite" onClick={this.makeFavorite} />
                                <span></span>
                            </div>
                        </div>
    
                        { (this.state.editForm === false)? (
                            <div className="media-right d-flex flex-column personalBoard_section align-content-center flex-wrap">
                                <div className="dropdown dropleft">
                                    <a className="pointer" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                        <div className="dotted_menu">
                                            <span></span><span></span><span></span>
                                        </div>
                                    </a>
                                    <div className="dropdown-menu board-action" aria-labelledby="dropdownMenuLink">
                                        <a className="dropdown-item pointer" onClick={this.edit}>
                                            <img src="asset/image/menu_createnote_icon.png"/>Edit
                                        </a>
                                        <a className="dropdown-item pointer" onClick={this.delete}>
                                            <img src="asset/image/delete.png"/>Delete
                                        </a>
                                        { (this.canShareBoard)? (
                                            <a className="dropdown-item pointer" onClick={this.share}>
                                                <img src="asset/image/menu_share_icon.png"/>Share
                                            </a>
                                        ) : null }

                                        <a className="dropdown-item pointer" onClick={this.tempBoard} data-toggle="modal" data-target="#import_bookmark_modal">
                                            <img src="asset/image/import.png"/>Import
                                        </a>
                                        <a className="dropdown-item pointer" onClick={this.exportBoard}>
                                            <img src="asset/image/export.png"/>Export
                                        </a>
                                        <a className="dropdown-item pointer" onClick={this.createDuplicate}>
                                            <img src="asset/image/menu_duplicate_icon.png"/>Duplicate
                                        </a>
                                    </div>
                                </div> 
                            </div>
                        ) : null }                   
                    </div>
                </div>
            </div>
        ) : null
    );
  }
}

export default BoardBlock;
