/* global chrome, $, tilt_direction, sync2, authController */

import React, { Component } from 'react';
import FolderBlock from './FolderBlock';

class Boards extends Component {

    constructor(props) {
        super(props);
        this.addFolder = this.addFolder.bind(this);
        this.folder = chrome.extension.getBackgroundPage().getFolder();
        this.service = chrome.extension.getBackgroundPage().getService();
        this.state = {
          folders: this.folder.get()
        }
    }

    addFolder() {

        var folderDetail = this.folder.create({
            title: ''
        });

        this.setState({
          folders: this.folder.get()
        });

        this.service.createOrUpdateFolder(folderDetail.id, function(response){ });
    }

    render() {
        return (
            <div className="animated fadeInUp">
                { this.state.folders.map((item, idx) => ( <FolderBlock item={item} /> )) }

                <div class="row redsec">
                    <div class="media align-items-center my-3 pointer fancyBtnBig">
                        <div class="media-left pr-2">
                            <img src="asset/image/plus_icon.png" height="15" />
                        </div>
                        <div class="media-body">
                        <h4 class="m-0" onClick={this.addFolder} >
                            Add Folder
                        </h4>
                        </div>
                    </div>	 
                </div>
            </div>           
        );
    }
}

export default Boards;
