/*global chrome, $*/

import React, { Component } from 'react';

class Profile extends Component {

    constructor(props) {
        super(props);

        this.service  = chrome.extension.getBackgroundPage().getService();
        this.auth  = chrome.extension.getBackgroundPage().getAuth();
        this.changePasswordSubmit = this.changePasswordSubmit.bind(this);
        this.changeHandler = this.changeHandler.bind(this);
        this.deleteAccount = this.deleteAccount.bind(this);
        this.state = {
            name: '',
            email: '',
            newsletter: false,
            current_password: '',
            new_password: '',
            confirm_password: '',
            errors: {},
            cpErrors: {},
            profileChangedSuccess: false,
            changePasswordSuccess: false,
            profileProcess: false,
            changePasswordProcess: false
        }
        var user = JSON.parse(localStorage.getItem('qleary_user'));
        if(user){
            this.state.name = user.name;            
            this.state.email = user.email;
            this.state.newsletter = (user.newsletter == 1);            
        }
    }
   
    profileSubmit(e){
        e.preventDefault();     

        var _ = this;
        if(this.handleValidation()){

            this.setState({ profileProcess: true });

            let para = new FormData();

            para.append('name', this.state.name);
            para.append('email', this.state.email);
            para.append('newsletter', (this.refs.newsletter.checked)? 1 : 0);

            this.service.post(this.service.version + 'profile', para).then(function(response) {
                
                if(response.success) {
                    var user = _.auth.user();

                    user.name = response.result.name;
                    user.email = response.result.email;                    
                    user.newsletter = response.result.newsletter;
                    _.auth.setUser(user);
                    _.setState({ profileChangedSuccess : true, profileProcess: false });
                    setTimeout(function() { 
                        _.setState({ profileChangedSuccess : false }); 
                    }, 5000);
                } 
                
            })
            .catch(function(response){
                response = JSON.parse(response);
                if(response.hasOwnProperty('email')) {
                    _.setState({ errors : { email: response.email[0] } });
                }
            });

        }
    }

    changePasswordSubmit(e) {
        e.preventDefault();

        var flag = true;
        var cpErrors = {};
 

        if(!this.refs.current_password.value) {
            cpErrors['current_password']  = "This field is required.";
            flag = false;
        }

        if(!this.refs.password.value) {
            cpErrors['password']  = "This field is required.";
            flag = false;
        }

        if(!this.refs.confirm_password.value) {
            cpErrors['confirm_password']  = "This field is required.";
            flag = false;
        } else if(this.refs.confirm_password.value !== this.refs.password.value) {
            cpErrors['confirm_password']  = "The password and confirm password should be the same.";
            flag = false;
        }


        this.setState({ cpErrors : cpErrors });

        if(flag) {

            this.setState({ changePasswordProcess: true });
            var _ = this;

            let para = new FormData();

            para.append('current_password', this.refs.current_password.value);
            para.append('new_password', this.refs.password.value);
            para.append('confirm_password', this.refs.confirm_password.value);

            this.service.post('admin/post-change-password', para).then(function(response) {
                
                if(response.success) {

                    _.setState({
                        current_password: '',
                        password: '',
                        confirm_password: '',
                        changePasswordSuccess : true
                    });

                    setTimeout(function() { 
                        _.setState({ changePasswordSuccess : false }); 
                    }, 5000);
                } else {
                    alert(response.message);
                }
                _.setState({ changePasswordProcess : false }); 
            })
            .catch(function(response){
                _.setState({ changePasswordProcess : true }); 
            });
        }
    }

    deleteAccount() {
        
        var that = this;
        if(window.confirm('Are you sure you want to delete your account? This will delete all the data from the server. You will never be able to recover your data.')) {
                

            $(this.refs.delete_account).html('Removing Please wait...');
            
            this.service.post(this.service.version + 'profile/remove', new FormData()).then(function(response) {
                  
                that.auth.logout();
                document.location.reload();
                
            })
            .catch(function(response){ console.log(response) });
        }
    }

    handleValidation() {

        var flag = true;
        var errors = {};

        if(!this.refs.name.value) {
            errors['name']  = "This field is required.";
            flag = false;
        }

        if(!this.refs.email.value) {
            errors['email']  = "This field is required.";
            flag = false;
        } else  if(!this.isEmailValid(this.refs.email.value)) {
            errors['email']  = "Please enter a valid email address.";
            flag = false;
        }

        this.setState({
			errors : errors
		});

        return flag;
    }
    
    isEmailValid(email) {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }

    changeHandler(event) {
        const name = event.target.name;

        this.setState({
            [name]: event.target.value
        });
    }
    render() {
        return (  
        <div className="white_box animated fadeInUp">              
            <form method="post" onSubmit= {this.profileSubmit.bind(this)}>
                <div class="row">
                    <div class="col-sm-12 pb-4">
                        <h4 class="headingSec">Profile Settings</h4>
                    </div>
                    <div class="col-sm-6">
                        <label class="text">Name</label>
                        <div class="form-group">
                            <input class="form-control" placeholder="Name" type="text" value={this.state.name} ref="name" onChange={this.changeHandler} name="name"  />
                            <span class="text-danger"><small>{ this.state.errors['name'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                    <label class="text">Email</label>
                        <div class="form-group">
                            <input class="form-control" placeholder="Email" type="text" ref="email" value={this.state.email} name="email" onChange={this.changeHandler} />
                            <span class="text-danger"><small>{ this.state.errors['email'] }</small></span>
                        </div>
                    </div>

                    <div class="col-sm-12 pb-4">
                        <label class="checkbox-inline text">
                            <input type="checkbox" value="1" className="width-auto" ref="newsletter" defaultChecked={this.state.newsletter} onChange={this.changeHandler} /> 
                            &nbsp; Receive updates and more. We will never spam you, don't worry!
                        </label>
                    </div>
                    { (this.state.profileChangedSuccess)? <div class="col-sm-12 mt-4" >
                        <div class="alert alert-success">
                            <strong>Success!</strong> Profile has been updated.
                        </div>
                    </div> : null }

                    <div class="col-sm-12 mt-4 text-center">
                        <button class="btn btn-primary kbd p-2 px-4 m-auto">
                            { (this.state.profileProcess)? 'Processing...' : 'Save' }
                        </button>
                    </div> 
                </div>
            </form>
            <form method="post" onSubmit= {this.changePasswordSubmit} >
                <div class="row">
                    <div class="col-sm-12 pb-4 mt-5">
                        <h4 class="headingSec">Change Password</h4>
                    </div>
                    <div class="col-sm-12">
                        <div class="form-group">                        
                            <input class="form-control" placeholder="Current Password" type="password" ref="current_password" name="current_password" value={this.state.current_password} onChange={this.changeHandler} />
                            <span class="text-danger"><small>{ this.state.cpErrors['current_password'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                        <div class="form-group">
                            <input class="form-control" placeholder="Password" type="password" ref="password" name="password" value={this.state.password} onChange={this.changeHandler}  />
                            <span class="text-danger"><small>{ this.state.cpErrors['password'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                        <div class="form-group">
                            <input class="form-control" placeholder="Confirm Password" type="password" ref="confirm_password" name="confirm_password" value={this.state.confirm_password} onChange={this.changeHandler}  />
                            <span class="text-danger"><small>{ this.state.cpErrors['confirm_password'] }</small></span>
                        </div>
                    </div>

                    { (this.state.changePasswordSuccess)? <div class="col-sm-12 mt-4">
                        <div class="alert alert-success">
                            <strong>Success!</strong> Password has been changed successful.
                        </div>
                    </div> : null }
                    
                    <div class="col-sm-12 mt-4 text-center">
                        <button class="btn btn-primary kbd p-2 px-4 m-auto">                            
                            { (this.state.changePasswordProcess)? 'Processing...' : 'Change' }
                        </button>
                    </div>
                </div> 
            </form>

            <div class="col-sm-12 mt-4 text-center">
                <button class="btn btn-danger kbd p-2 px-4 m-auto" ref onClick={this.deleteAccount} ref="delete_account" >Delete account</button>
            </div> 

        </div>   
    );
  }
}

export default Profile;
