/* global make_csc_entry_sortable */
import React, { Component } from 'react';
import Header from './layout/Header';
import SideMenu from './layout/SideMenu';
import Profile from './layout/Profile';
import Boards from './layout/Boards';
import AccountSetting from './layout/AccountSetting';
import WhatsNew from './layout/WhatsNew';
import PublicBoard from './layout/PublicBoard';
import SharedBoard from './layout/SharedBoard';
import Billing from './layout/Billing';
import Invoice from './layout/Invoice';

class App extends Component {
  

  constructor(props) {
    super(props);
    this.changeMenu = this.changeMenu.bind(this);
    
    var url = new URL(window.location.href);

    this.state = { 
      currentMenu: url.searchParams.get("page") || 'boards'
    };

  }

  changeMenu(menu) {
    if(['profile', 'billing'].indexOf(menu) > -1) {
      if(!JSON.parse(localStorage.getItem('qleary_user'))){
        document.getElementsByClassName('open-signup')[0].click();
        return;
      }      
    }
    this.setState({ currentMenu: menu })
  }
  
  componentDidMount() {
    make_csc_entry_sortable();
  } 

  render() {
    return (   
      <div>
        <Header />
        <div class="container pt-4">
          <div class="row">    
              <div class="col-sm-4 col-md-3" id="side_menu">
                <SideMenu parentMethod={this.changeMenu} currentMenu={this.state.currentMenu} />
              </div>
            <div class="col-sm-8 col-md-9">
            { this.state.currentMenu == 'boards' ? <Boards /> 
              : this.state.currentMenu == 'public_boards' ? <PublicBoard /> 
              : this.state.currentMenu == 'shared_boards' ? <SharedBoard />
              : this.state.currentMenu == 'profile' ? <Profile />
              : this.state.currentMenu == 'billing' ? <Billing />
              : this.state.currentMenu == 'invoice' ? <Invoice />
              : this.state.currentMenu == 'account-setting' ? <AccountSetting />
              : this.state.currentMenu == 'whatsnew' ? <WhatsNew />
              : this.state.currentMenu == 'invite_friends' ? <WhatsNew /> : <Boards />
             }
                        
            </div>  
          </div>
        </div>
      </div>
      
    );
  }
}

export default App;
