/* global boardController, users, auth */
import React, { Component } from 'react';
import BoardBlock from './BoardBlock';


class SharedBoard extends Component {

    constructor(props) {
        super(props);

        this.state = {
            sharedWithMe: [],
            sharedByMe: []
        };
    }
    componentDidMount() {

        if(auth.isLoggedIn()) {
            var user_id = auth.user().id;
            
            var ids = [];
            users.get().forEach( item => { ids = ids.concat(item.board_ids); } );


            this.setState({
                sharedByMe: boardController.get().filter( item => {
                    return (ids.indexOf(item.id) !== -1 && item.user_id === user_id)
                }),
                sharedWithMe: boardController.get().filter( item => {
                    return item.user_id && item.user_id !== user_id;
                })
            });

            
        }

    }

    render() {

         return (<div className="animated fadeInUp">
            <div class="row board-row " ref="sortable">
                <div class="col-sm-12 media align-items-center my-3">
                    <div class="media-left pr-2">
                        <img src="asset/image/folder.png" height="15" />
                    </div>
                    <div class="media-body">
                        <h4 class="media-body m-0"> 
                            Shared With Me
                        </h4>
                    </div>
                </div> 
                {(this.state.sharedWithMe.length < 1)? (
                    <div class="col-sm-12 pb-4 mt-5 text-center mb-5">
                        <a href="https://help.qlearly.com/v2/how-to-privately-share-boards" target="_blank" className="link-hover text">
                            <h4>No board has been shared with you yet.</h4>
                        </a>
                    </div>
                ) : (
                    this.state.sharedWithMe.map((board) => ( <BoardBlock board={board} share="false" /> ))
                )}
            </div>
            <div class="row board-row " ref="sortable">
                <div class="col-sm-12 media align-items-center my-3">
                    <div class="media-left pr-2">
                        <img src="asset/image/folder.png" height="15" />
                    </div>
                    <div class="media-body">
                        <h4 class="media-body m-0"> 
                            Shared By Me
                        </h4>
                    </div>
                </div> 
                {(this.state.sharedByMe.length < 1)? (
                    <div class="col-sm-12 pb-4 mt-5 text-center mb-5">                        
                        <a href="https://help.qlearly.com/v2/how-to-privately-share-boards" target="_blank" className="link-hover text">
                            <h4>You have not shared any board.</h4>
                        </a>                        
                    </div>
                ) : (
                    this.state.sharedByMe.map((board) => ( <BoardBlock board={board} /> ))
                )}
            </div>
        </div>);
    }
}

export default SharedBoard;
