/* global chrome, $, tilt_direction */

import React, { Component } from 'react';
import BoardBlock from './BoardBlock';

class PublicBoard extends Component {
  constructor(props) {
    super(props);

    this.board = chrome.extension.getBackgroundPage().getBoard();
    this.state = {
      boards: this.board.get()
    };
  }
  test() {
    return (
      <div className="row">
        { this.state.boards.map((board) => ( (board.sharable_code)? <BoardBlock board={board} /> : null )) }
      </div>
    );
  }

  render() {
    return (  
      <div className="card">
          <div className="card-body">
            <div class="col-sm-12 pb-4 mt-5">
                  <h4 class="text-center text"> Coming soon! </h4>
          </div>
          </div>
      </div> 
    );
  }
}

export default PublicBoard;

